<template>
  <div class="wrapper">
    <!-- <Cover class="cover" /> -->
    <div class="book" @click="toggleTopFoot">
      <Top class="top" ref="top" />
      <div class="main">
        <router-view />
        <Nav class="nav" />
      </div>
      <Foot class="foot" ref="foot" />
    </div>
  </div>
</template>
<script>

import Nav from './Nav'
import Top from './topFoot/Top'
import Foot from './topFoot/Foot'
import Cover from './topFoot/Cover'
export default {
  data() {
    return {
      // flag: true,
      TFflag: true,
      exclude: ['Unit 1 Communication', 'Part One Reading', 'Part Two Grammar Focus', '', 'Part Three Practical Writing', 'Part Four Translation Focus', '', '[+]', '[-]']

    }
  },
  components: {
    Nav,
    Top,
    Foot,
    Cover,
  },
  mounted() {
  },

  mounted() {
    this.$root.eventHub.$on('toggleTopFoot', (e) => {  //top.vue
      this.toggleTopFoot(e)
    })
  },

  methods: {
    toggleTopFoot(e) {
      let target = e.target,
        top = this.$refs.top.$el,
        foot = this.$refs.foot.$el

      if (target.parentNode.id !== 'isToggle' || target.className.split(' ')[1] === 'nav') {
        if (this.TFflag && target.className.split(' ')[0] !== 'audio' && target.className !== 'symbol' && target.className.split(' ')[0] !== 'CH_EN' && target.className !== 'preview' && target.className !== 'next') {
          top.style.transform = `translateY(0%)`
          foot.style.transform = `translateY(0%)`
          this.TFflag = false
        } else {
          top.style.transform = `translateY(-100%)`
          foot.style.transform = `translateY(100%)`
          this.TFflag = true
        }
      }


    },
  }

}
</script>
<style lang='stylus' scoped>
.wrapper
  width 100%
  .cover
    position fixed 
    width 100%
    height 100%
    top 0
    left 0
    right 0
    bottom 0
    z-index 2
  .book
    position relative
    .main
      position relative
      box-sizing border-box
      .nav
        position fixed
        top 0
        left 0
        width 80%
    .top, .foot
      transition 800ms
      transform translateY(-100%)
      position fixed
      left 0
      z-index 2
    .foot
      bottom 0
      transform translateY(100%)



</style>